.d3-tip {
  color: #fff;
  pointer-events: none;
  background: #000c;
  border-radius: 2px;
  padding: 12px;
  font-weight: bold;
  line-height: 1;
}

.d3-tip:after {
  box-sizing: border-box;
  color: #000c;
  pointer-events: none;
  width: 100%;
  font-size: 10px;
  line-height: 1;
  position: absolute;
}

.d3-tip.n:after {
  content: "▼";
  text-align: center;
  margin: -1px 0 0;
  top: 100%;
  left: 0;
}

.d3-tip.e:after {
  content: "◀";
  margin: -4px 0 0;
  top: 50%;
  left: -8px;
}

.d3-tip.s:after {
  content: "▲";
  text-align: center;
  margin: 0 0 1px;
  top: -8px;
  left: 0;
}

.d3-tip.w:after {
  content: "▶";
  margin: -4px 0 0 -1px;
  top: 50%;
  left: 100%;
}
